@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@900&family=Roboto&family=Work+Sans:wght@100;200;400;700&display=swap);
:root {
    --main-bg-color: #191c2c;
    --dark-text: #191c2cbb;
    --red: #d7263d;
    --dark-red: #961b2c;
    --light-red: #d83b50;
    --gold: #fcca46;
    --white: #f3f4f6;
    --opal: #a4bab7;
    --dark-opal: #687c7bb6;
    --transparent-white: #f3f4f628;
}

html,
body {
    width: 100%;
    height: 100%;
}

html {
    overflow-y: scroll;
}

body {
    background-color: #191c2c;
    background-color: var(--main-bg-color);
    color: #f3f4f6;
    color: var(--white);
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}

h1 {
    -webkit-margin-before: 40px;
            margin-block-start: 40px;
    font-size: 90px;
    font-family: 'Work Sans', sans-serif;
}

h2 {
    font-size: 25px;
    font-weight: 400;
}

.flex-center {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.centered-text {
    text-align: center;
}

.bottom-right-fixed {
    font-family: 'Work Sans', sans-serif;
    position: fixed;
    bottom: 0px;
    right: 20px;
    font-weight: 200;
}

.red {
    color: #d7263d;
    color: var(--red);
}

.red-highlight {
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 25px;
    background-image: linear-gradient(90deg, transparent 0%, #fcca46);
    background-image: linear-gradient(90deg, transparent 0%, var(--gold));
    background-position: 0% 110%;
    background-size: auto;
    background-repeat: repeat-x;
    background-size: 200% 50%;
    transition: background-position 0.5s;
}

.show-highlight {
    background-position: -100% 150%;
    transition: background-position 1s;
}

::-webkit-scrollbar {
    background-color: rgba(0, 0, 0, 0);
    margin: 1px;
    width: 0.8em;
}

::-webkit-scrollbar-thumb:window-inactive,
::-webkit-scrollbar-thumb {
    background: rgba(119, 119, 119, 0.658);
    border-radius: 10px;
}
::-webkit-scrollbar-corner {
    background: rgba(0, 0, 0, 0);
}

/* Created by Alison Quaglia */
/* https://www.sliderrevolution.com/resources/css-animated-background/ */
body {
    /* background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab); */
    /* background: linear-gradient(-45deg, var(--dark-opal), var(--gold), var(--light-red)); */
    background: linear-gradient(-45deg, #6e8387, #a4b8c4, #1b2845);
    background-size: 400% 400%;
    -webkit-animation: gradient 15s ease infinite;
            animation: gradient 15s ease infinite;
}

@-webkit-keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

/* If the screen size is 601px wide or more, set the font-size of <div> to 80px */
@media screen and (max-width: 600px) {
    h1 {
        font-size: 50px;
    }
}

.NominationDisplay_li {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.NominationDisplay_wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 300px;
    padding: 20px;
    padding-bottom: 0;
}

.NominationDisplay_wrapper:hover {
    background-color: var(--transparent-white);
    cursor: pointer;
}

.NominationDisplay_filler {
    display: block;
    height: 300px;
    width: 200px;
    background-color: var(--dark-opal);
}

.NominationDisplay_img {
    display: block;
}
.NominationDisplay_wrapper:hover:after {
    content: '\00d7'; /* This will render the 'X' */
    color: var(--red);
    position: relative;
    top: -250px;
    -webkit-transform: scale(6);
            transform: scale(6);
    height: 0;
}

.NominationDisplay_img_wrapper {
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
}

.NominationsList_wrapper {
    display: block;
    margin: 0 auto;
    width: 95%;
}

.NominationsList_heading {
    margin-top: 10px;
    margin-bottom: 0px;
}
.NominationsList_p {
    margin-top: 0px;
}

.NominationsList_ul {
    padding: 0;
    list-style-type: none;
    list-style-image: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 20px;
}

.SearchBar_wrapper {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 auto;
    width: 50%;
}

/* Fixes internal padding / width issue */
input.input {
    width: 100%;
    padding-left: 8%; /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
    box-sizing: border-box; /* Opera/IE 8+ */
}

.SearchBar_input {
    display: inline-block;
    margin: 0 auto;
    width: 100%;
    padding: 20px;
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 25px;
    font-family: 'Work Sans', sans-serif;
    font-weight: 600;
    color: var(--dark-text);
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
}

.SearchBar_input::-webkit-input-placeholder {
    opacity: 0.5;
    font-style: italic;
}

.SearchBar_input:-ms-input-placeholder {
    opacity: 0.5;
    font-style: italic;
}

.SearchBar_input::placeholder {
    opacity: 0.5;
    font-style: italic;
}

.SearchBar_input:focus {
    outline: none;
    box-shadow: 0 0 3pt 2pt var(--gold);
}

.SearchBar_ul {
    overflow-y: scroll;
    overflow-x: hidden;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    transition: height 0.3s ease;
    position: relative;
    top: -35px;
    list-style-type: none;
    list-style-image: none;
}

.SearchBar_spinner {
    position: relative;
    bottom: 53px;
    left: calc(50% - 35px);
    z-index: 100;
    transition: opacity 0.2s ease;
}
/* https://codepen.io/mandelid/pen/vwKoe */
#loading {
    display: inline-block;
    width: 30px;
    height: 30px;
    border: 3px solid var(--opal);
    border-radius: 50%;
    border-top-color: var(--dark-opal);
    animation: spin 1s ease-in-out infinite;
    -webkit-animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
    to {
        -webkit-transform: rotate(360deg);
    }
}
@-webkit-keyframes spin {
    to {
        -webkit-transform: rotate(360deg);
    }
}

/* Yoinked from https://codepen.io/web-tiki/pen/ByqBVB, author: web-tiki */

.CompletionBanner_wrapper {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    transition: 0.5s height ease, 0.5s opacity ease;
}

.ribbon {
    font-size: 20px;
    position: relative;
    display: inline-block;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 30px;
}
.text {
    display: inline-block;
    padding: 0.5em 1em;
    min-width: 20em;
    line-height: 1.2em;
    background: var(--red);
    position: relative;
}
.ribbon:after,
.ribbon:before,
.text:before,
.text:after,
.bold:before {
    content: '';
    position: absolute;
    border-style: solid;
}
.ribbon:before {
    top: 0.3em;
    left: 0.2em;
    width: 100%;
    height: 100%;
    border: none;
    z-index: -2;
}
.text:before {
    bottom: 100%;
    left: 0;
    border-width: 0.5em 0.7em 0 0;
    border-color: transparent var(--dark-red) transparent transparent;
}
.text:after {
    top: 100%;
    right: 0;
    border-width: 0.5em 2em 0 0;
    border-color: var(--light-red) transparent transparent transparent;
}
.ribbon:after,
.bold:before {
    top: 0.5em;
    right: -2em;
    border-width: 1.1em 1em 1.1em 3em;
    border-color: var(--dark-red) transparent var(--dark-red) var(--dark-red);
    z-index: -1;
}

@media screen and (max-width: 600px) {
    p.ribbon {
        font-size: 15px;
    }
}

